import React from 'react';

const Users = React.lazy(() => import('./views/Users/Users'));
const User = React.lazy(() => import('./views/Users/User'));
const AddUser = React.lazy(() => import('./views/Users/AddUser'));

const Admins = React.lazy(() => import('./views/Admins/Admins'));
const Admin = React.lazy(() => import('./views/Admins/Admin'));

const Courses = React.lazy(() => import('./views/Courses/Courses'));
const Course = React.lazy(() => import('./views/Courses/Course'));

const Scholarships = React.lazy(() => import('./views/Scholarships/Scholarships'));
const Scholarship = React.lazy(() => import('./views/Scholarships/Scholarship'));

const Groups = React.lazy(() => import('./views/Groups/Groups'));
const Group = React.lazy(() => import('./views/Groups/Group'));

const Categories = React.lazy(() => import('./views/Categories/Categories'));
const Category = React.lazy(() => import('./views/Categories/Category'));

const Tags = React.lazy(() => import('./views/Tags/Tags'));
const Tag = React.lazy(() => import('./views/Tags/Tag'));

const EdParties = React.lazy(() => import('./views/EdParties/EdParties'));
const EdPartie = React.lazy(() => import('./views/EdParties/EdPartie'));

const Schools = React.lazy(() => import('./views/Schools/Schools'));
const School = React.lazy(() => import('./views/Schools/School'));

const Teachers = React.lazy(() => import('./views/Teachers/Teachers'));
const Teacher = React.lazy(() => import('./views/Teachers/Teacher'));

const Charts = React.lazy(() => import('./views/Charts'));
const Dashboard = React.lazy(() => import('./views/Dashboard'));
const Colors = React.lazy(() => import('./views/Theme/Colors'));
const Typography = React.lazy(() => import('./views/Theme/Typography'));
const Widgets = React.lazy(() => import('./views/Widgets/Widgets'));

// https://github.com/ReactTraining/react-router/tree/master/packages/react-router-config
const routes = [
  { path: '/', exact: true, name: 'Home' },
  { path: '/users', exact: true, name: 'Users', component: Users },
  { path: '/users/add', exact: true, name: 'Add User', component: AddUser },
  { path: '/users/:id', exact: true, name: 'User Details', component: User },
  { path: '/admins', exact: true, name: 'Admins', component: Admins },
  { path: '/admins/:id', exact: true, name: 'Admin Details', component: Admin },
  { path: '/courses', exact: true, name: 'Courses', component: Courses },
  { path: '/courses/:id', exact: true, name: 'Course Details', component: Course },
  { path: '/scholarships', exact: true, name: 'Scholarships', component: Scholarships },
  { path: '/scholarships/:id', exact: true, name: 'Scholarship Details', component: Scholarship },
  { path: '/groups', exact: true, name: 'Groups', component: Groups },
  { path: '/groups/:id', exact: true, name: 'Group Details', component: Group },
  { path: '/edParties', exact: true, name: 'EdParties', component: EdParties },
  { path: '/edParties/:id', exact: true, name: 'EdPartie Details', component: EdPartie },
  { path: '/schools', exact: true, name: 'Schools', component: Schools },
  { path: '/schools/:id', exact: true, name: 'School Details', component: School },
  { path: '/teachers', exact: true, name: 'Teachers', component: Teachers },
  { path: '/teachers/:id', exact: true, name: 'Teacher Details', component: Teacher },
  { path: '/categories', exact: true, name: 'Categories', component: Categories },
  { path: '/categories/:id', exact: true, name: 'Category Details', component: Category },
  { path: '/tags', exact: true, name: 'Tags', component: Tags },
  { path: '/tags/:id', exact: true, name: 'Tag Details', component: Tag },
  { path: '/dashboard', name: 'Dashboard', component: Dashboard },
  { path: '/theme', exact: true, name: 'Theme', component: Colors },
  { path: '/theme/colors', name: 'Colors', component: Colors },
  { path: '/theme/typography', name: 'Typography', component: Typography },
  { path: '/widgets', name: 'Widgets', component: Widgets },
  { path: '/charts', name: 'Charts', component: Charts },
];

export default routes;
